.page_contact_head {
  display: grid;
  grid-template-columns: 6fr 14fr;
  margin-bottom: 50px;
  > div {
    &:not(.global_line) {
      padding: 5px var(--gutter) 3px;
    }
  }
}
.page_contact_head-subtitle {
  text-transform: uppercase;
  letter-spacing: 0.02em;
}
.page_contact_detail {
  display: grid;
  padding-bottom: 50px;
  > div {
    padding: 0 var(--gutter);
    white-space: pre-line;
    > * {
      &:nth-child(n + 2) {
        margin-top: 1em;
      }
    }
  }
}
.page_contact_detail-description {
  p {
    width: 100%;
    max-width: 280px;
  }
}
.page_contact_form-form {
  grid-column: 2/3;
  padding: 0 var(--gutter);
  > h3 {
    margin-bottom: 5px;
    text-transform: uppercase;
    letter-spacing: 0.02em;
  }
}
.page_contact_form-fields {
  display: flex;
  flex-direction: column;
  > * {
    &:not(button) {
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      border: none;
      border-bottom: 1px solid var(--color-brown);
      background: none;
      padding: 5px 0;
      margin-top: -1px;
      cursor: text;
    }
  }
  textarea {
    border-top: 1px solid var(--color-brown);
  }
  > button {
    margin-top: 20px;
  }
}
@media screen and (min-width: 820px) {
  .page_contact_head-subtitle {
    grid-row: 3/4;
    grid-column: 2/3;
  }
  .page_contact_detail {
    grid-template-columns: 6fr 8fr 6fr;
  }
  .page_contact_form {
    display: grid;
    grid-template-columns: 6fr 14fr;
  }
}
@media screen and (max-width: 820px) {
  .page_contact_head {
    order: -2;
    &::before {
      display: none;
    }
    .global_line {
      display: none;
    }
  }
  .page_contact_head-title {
    display: none;
  }
  .page_contact_detail {
    display: contents;
  }
  .page_contact_detail-social {
    order: 1;
    margin-top: 50px;
  }
  .page_contact_detail-description {
    order: -1;
    margin-bottom: 100px;
  }
  .page_contact_form-form {
    h3 {
      padding: 4px 0 2px;
      border-top: 1px solid var(--color-brown);
      margin-bottom: 0;
    }
  }
}
